import { Collapse } from "antd";
import { CInput, CSelect, CTextArea, CDatePicker } from "@components";

const CFixAssetTecnicalInformation= ()=>{
    const { Panel } = Collapse
    return (
      <>
         <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
            <Panel header="Tecnical Information" key="0">
               <fieldset>
                  <legend>MODEL</legend>
                  <CInput
                     label="Make"
                     name="make"
                     id="make"
                     isRequired
                     autoFocus
                  />

                  <CInput
                     label="Model"
                     name="model"
                     id="model"
                     isRequired
                     autoFocus
                  />

                  <CInput
                     label="Model Year"
                     name="model_year"
                     id="model_year"
                     isRequired
                     autoFocus
                  />

                  <CInput
                     label="Serial Number"
                     name="serial_number"
                     id="serial_number"
                     isRequired
                     autoFocus
                  />

                  <CDatePicker
                     label="Guarantee Date"
                     name="general_date"
                     id="general_date"
                     isRequired
                     autoFocus
                  />

                  <CInput
                     label="Technical Information 1"
                     name="technical_info_1"
                     id="technical_info_1"
                     autoFocus
                  />

                  <CInput
                     label="Technical Information 2"
                     name="technical_info_2"
                     id="technical_info_2"
                     autoFocus
                  />

                  <CInput
                     label="Technical Information 3"
                     name="technical_info_3"
                     id="technical_info_3"
                     autoFocus
                  />
               </fieldset>

               <fieldset>
                  <legend>UPDATE</legend>
                  <CDatePicker
                     label="Last Maintenance"
                     name="last_maintenance"
                     id="last_maintenance"
                     autoFocus
                  />

                  <CDatePicker
                     label="Next Maintenance"
                     name="next_maintenance"
                     id="next_maintenance"
                     autoFocus
                  />

                  <CInput
                     label="Information 1"
                     name="information_1"
                     id="information_1"
                     isRequired
                     autoFocus
                  />

                  <CInput
                     label="Information 2"
                     name="information_2"
                     id="information_2"
                     isRequired
                     autoFocus
                  />

                  <CTextArea
                     label="Information 3"
                     name="information_3"
                     id="information_3"
                     isRequired
                     autoFocus
                  />

                  <CSelect
                     label="Assets Condition"
                     name="assets_condition"
                     id="assets_condition"
                     autoFocus
                  />
               </fieldset>
            </Panel>
         </Collapse>
      </>
    )
}
export default CFixAssetTecnicalInformation;